<template>
  <a-layout class="main-layout">
    <a-layout-header class="header">
      <div class="logo">管理系统</div>
      <div class="user-info" v-if="user">
        <a-avatar :src="user.avatar" />
        <span class="username">{{ user.username }}</span>
      </div>
    </a-layout-header>
    <a-layout>
      <a-layout-sider width="200" style="background: #fff">
        <a-menu
          mode="inline"
          :selectedKeys="[selectedKey]"
          :defaultOpenKeys="['task']"
          style="height: 100%"
        >
          <a-menu-item key="user-management" @click="() => navigate('user-management')">
            <a-icon type="user" />
            <span>用户管理</span>
          </a-menu-item>
          <a-menu-item key="class-management" @click="() => navigate('class-management')">
            <a-icon type="team" />
            <span>班级管理</span>
          </a-menu-item>
          
          <!-- 新增练习任务菜单 -->
          <a-sub-menu key="task">
            <span slot="title">
              <a-icon type="experiment" />
              <span>练习任务</span>
            </span>
            <a-menu-item key="2d-annotation" @click="() => navigate('task/2d-annotation')">
              <a-icon type="border" />
              <span>2D标注</span>
            </a-menu-item>
            <a-menu-item key="3d-annotation" @click="() => navigate('task/3d-annotation')">
              <a-icon type="box-plot" />
              <span>3D标注</span>
            </a-menu-item>
            <a-menu-item key="audio-annotation" @click="() => navigate('task/audio-annotation')">
              <a-icon type="sound" />
              <span>音频标注</span>
            </a-menu-item>
          </a-sub-menu>
          
          <a-menu-item key="logout" @click="logout">
            <a-icon type="logout" />
            <span>退出系统</span>
          </a-menu-item>
        </a-menu>
      </a-layout-sider>
      <a-layout-content class="content">
        <div class="content-wrapper">
          <router-view />
        </div>
      </a-layout-content>
    </a-layout>
  </a-layout>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      selectedKey: this.getInitialSelectedKey()
    }
  },
  computed: {
    ...mapGetters({
      user: 'currentUser'
    })
  },
  watch: {
    $route() {
      this.updateSelectedKey()
    }
  },
  methods: {
    navigate(path) {
      this.$router.push(`/${path}`)
    },
    logout() {
      this.$store.dispatch('logout')
      this.$router.push('/login')
      this.$message.success('已成功退出系统')
    },
    updateSelectedKey() {
      const path = this.$route.path
      if (path.includes('user-management')) {
        this.selectedKey = 'user-management'
      } else if (path.includes('class-management')) {
        this.selectedKey = 'class-management'
      } else if (path.includes('2d-annotation')) {
        this.selectedKey = '2d-annotation'
      } else if (path.includes('3d-annotation')) {
        this.selectedKey = '3d-annotation'
      } else if (path.includes('audio-annotation')) {
        this.selectedKey = 'audio-annotation'
      }
    },
    getInitialSelectedKey() {
      const path = this.$route.path
      if (path.includes('user-management')) {
        return 'user-management'
      } else if (path.includes('class-management')) {
        return 'class-management'
      } else if (path.includes('2d-annotation')) {
        return '2d-annotation'
      } else if (path.includes('3d-annotation')) {
        return '3d-annotation'
      } else if (path.includes('audio-annotation')) {
        return 'audio-annotation'
      }
      return 'user-management'
    }
  },
  created() {
    this.updateSelectedKey()
  }
}
</script>

<style scoped>
.main-layout {
  min-height: 100vh;
}

.header {
  background: #fff;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  z-index: 1;
}

.logo {
  font-size: 18px;
  font-weight: bold;
  color: #1890ff;
}

.user-info {
  display: flex;
  align-items: center;
}

.username {
  margin-left: 8px;
  font-size: 14px;
}

.content {
  padding: 24px;
  background: #f0f2f5;
  min-height: 280px;
}

.content-wrapper {
  padding: 24px;
  background: #fff;
  min-height: 100%;
  border-radius: 4px;
}
</style> 