<template>
  <div class="login-container">
    <div class="login-form-container">
      <h2 class="login-title">系统登录</h2>
      <a-form
        id="loginForm"
        :form="form"
        class="login-form"
        @submit="handleSubmit"
      >
        <a-form-item>
          <a-input
            v-decorator="[
              'username',
              { rules: [{ required: true, message: '请输入账号!' }] }
            ]"
            placeholder="请输入账号"
            size="large"
          >
            <a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
          </a-input>
        </a-form-item>

        <a-form-item>
          <a-input-password
            v-decorator="[
              'password',
              { rules: [{ required: true, message: '请输入密码!' }] }
            ]"
            placeholder="请输入密码"
            size="large"
          >
            <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
          </a-input-password>
        </a-form-item>

        <a-form-item>
          <div class="captcha-container">
            <a-input
              v-decorator="[
                'captcha',
                { rules: [{ required: true, message: '请输入验证码!' }] }
              ]"
              placeholder="请输入验证码"
              style="width: 200px"
              size="large"
            >
              <a-icon slot="prefix" type="safety" style="color: rgba(0,0,0,.25)" />
            </a-input>
            <div class="captcha-image" @click="refreshCaptcha">
              {{ captchaText }}
            </div>
          </div>
        </a-form-item>

        <a-form-item>
          <a-button
            type="primary"
            html-type="submit"
            class="login-form-button"
            size="large"
            :loading="loading"
          >
            登录
          </a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: this.$form.createForm(this),
      loading: false,
      captchaText: this.generateCaptcha()
    }
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.loading = true
          
          // 简单的验证码检查
          if (values.captcha !== this.captchaText) {
            this.$message.error('验证码错误!')
            this.loading = false
            this.refreshCaptcha()
            return
          }
          
          // 在实际项目中，这里应该调用API进行验证
          // 这里模拟登录过程
          setTimeout(() => {
            this.loading = false
            this.$store.dispatch('login', {
              username: values.username,
              avatar: 'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png'
            })
            this.$router.push('/user-management')
            this.$message.success('登录成功!')
          }, 1000)
        }
      })
    },
    refreshCaptcha() {
      this.captchaText = this.generateCaptcha()
    },
    generateCaptcha() {
      // 简单的验证码生成，实际项目中应该从后端获取
      const chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'
      let result = ''
      for (let i = 0; i < 4; i++) {
        result += chars.charAt(Math.floor(Math.random() * chars.length))
      }
      return result
    }
  }
}
</script>

<style scoped>
.login-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.login-form-container {
  width: 400px;
  padding: 40px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  animation: fadeIn 0.8s ease;
}

.login-title {
  text-align: center;
  margin-bottom: 30px;
  color: #1890ff;
  font-size: 24px;
}

.login-form-button {
  width: 100%;
}

.captcha-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.captcha-image {
  width: 120px;
  height: 40px;
  background: #f2f2f2;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 4px;
  color: #333;
  cursor: pointer;
  user-select: none;
  margin-left: 10px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-form input, .login-form .ant-input-affix-wrapper {
  transition: all 0.3s;
}

.login-form input:focus, .login-form .ant-input-affix-wrapper:focus {
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}
</style> 