import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: JSON.parse(localStorage.getItem('user')) || null
  },
  mutations: {
    SET_USER(state, user) {
      state.user = user
      localStorage.setItem('user', JSON.stringify(user))
    },
    CLEAR_USER(state) {
      state.user = null
      localStorage.removeItem('user')
      localStorage.removeItem('token')
    }
  },
  actions: {
    login({ commit }, user) {
      // 这里应该有一个实际的登录API请求
      // 为演示目的，我们直接设置
      commit('SET_USER', user)
      localStorage.setItem('token', 'demo-token')
    },
    logout({ commit }) {
      commit('CLEAR_USER')
    }
  },
  getters: {
    loggedIn(state) {
      return !!state.user
    },
    currentUser(state) {
      return state.user
    }
  }
}) 